import * as Yup from 'yup';

export const advertValidationSchema = Yup.object({
  photo: Yup.mixed()
    .required(' ')
    .test('is-file', ' ', value => {
      return !!value;
    }),
  price: Yup.number()
    .required(' ')
    .min(1, '       '),
  title: Yup.string()
    .required(' ')
    .min(3, '       3 ')
    .max(30, '    30 '),
  description: Yup.string()
    .required(' ')
    .min(3, '       3 ')
    .max(90, '    90 '),
  position: Yup.string()
    .required(' ')
    .min(3, ' 3   '),
});
